<script lang="ts">
    import {
        Collapse,
        Navbar,
        NavbarToggler,
        NavbarBrand,
        Nav,
        NavItem,
        NavLink,
        Dropdown,
        DropdownToggle,
        DropdownMenu,
        DropdownItem
    } from 'sveltestrap';

    var screenWidth = window.innerWidth;

    window.addEventListener("resize", function(event) {
        screenWidth = window.innerWidth;
    })

    let isOpen = false;

    function handleUpdate(event) {
        isOpen = event.detail.isOpen;
    }
</script>

<header>
  <Navbar color="light" light expand="md">
    <NavbarBrand href="/game">
      <div class="logo"><span>A</span>NIMAL<span>H</span><img src="images/dog-house.png" alt="logo" />USE</div>
      <div class="slogan">FOR YOUR PETS AND FOR YOUR MIND</div>
    </NavbarBrand>
    <NavbarToggler on:click={() => (isOpen = !isOpen)} />
    <Collapse {isOpen} navbar expand="md" on:update={handleUpdate}>
      <Nav class="ms-auto" navbar>
        <NavItem>
          <NavLink href="#components/">Components</NavLink>
        </NavItem>
        <NavItem>
          <NavLink href="https://github.com/bestguy/sveltestrap">GitHub</NavLink>
        </NavItem>
        <Dropdown nav inNavbar>
          <DropdownToggle nav caret>Options</DropdownToggle>
          <DropdownMenu end>
            <DropdownItem>Option 1</DropdownItem>
            <DropdownItem>Option 2</DropdownItem>
            <DropdownItem divider />
            <DropdownItem>Reset</DropdownItem>
          </DropdownMenu>
        </Dropdown>
      </Nav>
    </Collapse>
  </Navbar>
  <div class='mininav' class:mobile={screenWidth<500}>
    <a href="/game">Home</a>
    <a href="/game/quiz">Quiz</a>
    <a href="/game/wordle">Wordle</a>
    <a href="/game/memory">Memory</a>
    <a href="/game/funnyvideos">Funny Videos</a>
    <a href="/game/animalinfo">Animal Info</a>
    <a href="/game/medinfo">Medical Info</a>
    <a href="/game/yourpets">Your Pets</a>
  </div>
</header>


