<script>
    import MainVideo from "./MainVideo.svelte";
    import Playlist from "./Playlist.svelte";


    var playlist = [];
    var currentVideo;
    var playlistId = "PLtDp75hOzOlbD7m-Gb2t4dZqyYx7dq0iB";
    var mainReady = false;

    fetch("https://youtube.googleapis.com/youtube/v3/playlistItems?part=snippet&playlistId="+playlistId+"&key=AIzaSyBwKTLoQHoNYRzU2f6laHTOrXILwMpbtnQ&maxResults=9")
	.then((result) => {
		return result.json();
	}).then((data) => {
        console.log(data)
        let first = true;
        for (let video of data.items){
            if (first){
                first = false;
                mainReady = true;
                currentVideo = video;
            }else 
            playlist = [...playlist, video]
        }
        /*for (let item of data.items){
            videosId = [...videosId, item.snippet.resourceId.videoId]
        } "https://www.youtube.com/embed/"*/
	})

    const switchVideo = (event) =>{
        let temp = playlist;
        let i = 0;
        playlist = [];
        for(let video of temp){
            if(i != event.detail.index)
                playlist = [...playlist, video]
            i++;
        }
        playlist = [...playlist, currentVideo];
        currentVideo = temp[event.detail.index];
    }

</script>

<div>
    {#if mainReady}
        <MainVideo {currentVideo}/> 
    {/if}
    <Playlist {playlist} on:videoChosen={switchVideo}/>
</div>

