<script>
    import Answers from "./Answers.svelte";
    import Question from "./Question.svelte";
    import Score from "./Score.svelte";
    import { createEventDispatcher } from 'svelte'
    import {tick} from "svelte";
    import LoadingScreen from "../loading/LoadingScreen.svelte";
    import { Popover } from 'sveltestrap';

    var questionCategory;
    var animalName;
    var correctAnswer;
    var animals;
    var quizReady = false;
    var attemptsLeft;
    var score;
    var pointsModifier = 5;
    var animals = [];

    var questionObject;
    var answersObject;

    var screenWidth = window.innerWidth;

    window.addEventListener("resize", function(event) {
        screenWidth = window.innerWidth;
    })

    const initQuiz = () => {
        attemptsLeft = 3;
        score = 0;
        
        chooseAnimal();
        createQuiz();
    };

    const chooseAnimal = () =>{
        let i = Math.floor(Math.random() * animals.length);
        animalName = animals[i];
    }

    const createQuiz = () =>{
        //questionCategory = "scientific name";
        var xmlHttp = new XMLHttpRequest();
	    xmlHttp.onreadystatechange = function() { 
            if (xmlHttp.readyState == 4 && xmlHttp.status == 200){
                let response = JSON.parse(xmlHttp.responseText);
                
                correctAnswer = questionObject.setQuestion(response[0]);
                tick().then(()=>{
                    answersObject.defineAnswers();
                    quizReady = true;
                })
            }
        }
        xmlHttp.open( "GET", 'https://api.api-ninjas.com/v1/animals?name=' + animalName, true ); 
        xmlHttp.setRequestHeader("X-Api-Key","XeRLqZeWmuiW7/PMyztdHQ==HoJJOzopIX90X1xe");
        xmlHttp.send( null );
    }
    
    const dispatch = createEventDispatcher()

    const updateScore = (event) => {
        const value = event.detail.pointsModifier
        score += value;
        if (value <= 0){
            if (--attemptsLeft <= 0){
                //partita finita
                dispatch('matchEnded')
            }
        }

        if (attemptsLeft>0){
            //prossima domanda
            setTimeout(function(){
                quizReady = false;
            }, 1000);
            setTimeout(chooseAnimal, 3000);
            setTimeout(createQuiz, 3000);
        }
    }

    setTimeout(function(){
        initQuiz();
    }, 7200);
</script>

<LoadingScreen/>
<div class="container">
    <Score {score} {attemptsLeft} {pointsModifier}/>
    <div class="quiz content" class:mobile={screenWidth<500}>
        <button id="info"><i class="bi-info-circle"></i></button>
        <Popover placement="left" target="info">
            <div slot="title">
            How to play <b>Quiz</b>
            </div>
            Get the highest <b>score</b> possible by guessing the right answer of each question. Pay attention: 
            if you guess more questions in a row your <b>combo</b> will increase and you will earn more and more points for each correct answer. 
            But, if you give the wrong answer you will have to start over with the basic combo. You have only 3 lives available,
             with the third wrong answer the game will end. 
        </Popover>
        <Question bind:animals={animals} bind:questionCategory={questionCategory} {animalName} {quizReady} bind:this={questionObject}/>
        <Answers bind:this={answersObject} {questionCategory} {correctAnswer} {quizReady} bind:pointsModifier={pointsModifier} on:answerChoosen={updateScore}/>
    </div>
</div>


<style>
    .quiz{
        margin-top: 4vh;
        width: 100%;
        height: 65vh;
        position: relative;
    }

    .mobile{
        height: 85vh;
    }

    #info{
        position: absolute;
        top: 0;
        right: 0;
        background-color: transparent;
        border: 1px solid transparent;
    }
</style>